import { FC } from 'react'
import { RouteComponentProps } from '@reach/router'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import Card from '@primer/components/Card'
import Form, { useForm } from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import Actions from '@primer/components/Actions'
import Stack from '@primer/components/Stack'

interface FormSchema {
	name: string
	startDate: Date
	options: {
		label: string
		value: number
	}[]
	list: string[]
	hash: {
		[x: string]: boolean
	}
}

const Test: FC<RouteComponentProps> = () => {
	const formContext = useForm<FormSchema>({
		initialValues: {
			name: '',
			startDate: new Date(),
			options: [
				{ label: 'Gold', value: 1 },
				{ label: 'Silver', value: 2 },
				{ label: 'Bronze', value: 3 },
			],
			list: [],
			hash: {
				a: true,
				b: false,
			},
		},
		onSubmit: (formData) => {
			console.log('formData', formData)
		},
	})

	// const { values, arrayHelpers } = useFieldArray('options', formContext)

	return (
		<>
			<PageHeading title="New Form Components" />

			<PageContent>
				<Form context={formContext}>
					<Card>
						<Stack>
							<FormInput name="name" label="Name" validations={{ required: 'Name is required' }} />

							<Actions
								actions={[
									{
										title: 'Submit',
										intent: 'primary',
										type: 'submit',
									},
								]}
							/>
						</Stack>
					</Card>
				</Form>
			</PageContent>
		</>
	)
}

export default Test

