import { CheckIcon, XIcon } from '@heroicons/react/outline'
import { format } from 'date-fns'
import db from 'db'
import { ValueModel } from 'db/types'

export const getEntityFieldValue = (val?: ValueModel) => {
	if (!val) return

	let field = db.fields.find(val.fieldId)

	switch (field?.type) {
		case 'string':
		case 'option':
			return val.stringValue
		case 'date':
			return val.dateValue ? format(val.dateValue, 'dd/MM/yyyy') : undefined
		case 'number':
			return val.numberValue
		case 'boolean':
			return val.booleanValue ? <CheckIcon className="w-4 h-4" /> : <XIcon className="w-4 h-4" />
	}
}

export const sortValuesByLatest = (a: ValueModel, b: ValueModel) => {
	if (a.created && b.created) return new Date(b.created).valueOf() - new Date(a.created).valueOf()
	return 0
}
