import { FC } from 'react'
import { Link, RouteComponentProps } from '@reach/router'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import Card from '@primer/components/Card'
import Actions from '@primer/components/Actions'
import { ArrowLeftIcon } from '@heroicons/react/outline'
import DataListColumns from '@primer/components/DataListColumns'
import { getEntityFieldValue } from 'utils/funcs'
import { unitResource } from 'db/types'

const ViewEntity: FC<RouteComponentProps<{ entityId: string; id: string }>> = ({ id = '' }) => {
	const unit = unitResource(id)
	const entity = unit?.entity
	const values = unit?.values

	const title = `View ${entity?.name}`

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: `/${entity?.key}`,
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Card>
					<DataListColumns
						items={
							values?.map((x, i) => {
								let hasHistory = x.history.length > 1

								return {
									title: x.field?.name,
									value: (
										<div>
											<div>{getEntityFieldValue(x)}</div>
											{hasHistory && (
												<div>
													<Link to={`${process.env.PUBLIC_URL}/value-history/${x?.id}`}>
														See history
													</Link>
												</div>
											)}
										</div>
									),
								}
							}) || []
						}
					/>
				</Card>
			</PageContent>
		</>
	)
}

export default ViewEntity
