import { ActionModel, EntityModel, FieldModel, RoleModel } from 'db/types'
import { v4 as id } from 'uuid'
import db, { clear, persist } from './index'

export const seed = () => {
	clear()
	persist()

	db.siteSettings.set('title', 'NZDRC2')
	db.siteSettings.set('layout', 'sidebar')

	// ENTITY TYPES

	const userEntity: EntityModel = {
		id: id(),
		key: 'user',
		name: 'User',
		plural: 'Users',
		mainNav: true,
	}

	const caseEntity: EntityModel = {
		id: id(),
		key: 'case',
		name: 'Case',
		plural: 'Cases',
		mainNav: true,
	}

	const eventEntity: EntityModel = {
		id: id(),
		key: 'event',
		name: 'Event',
		plural: 'Events',
		mainNav: false,
	}

	db.entities.save(userEntity)
	db.entities.save(caseEntity)
	db.entities.save(eventEntity)

	// META TYPES

	// CASE

	const caseFields: FieldModel[] = [
		{
			id: id(),
			entityId: caseEntity.id,
			key: 'case-number',
			name: 'Case Number',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityId: caseEntity.id,
			key: 'type',
			name: 'Type',
			type: 'option',
			options: ['Review', 'Mediation', 'Hybrid'],
			defaultValue: 'Review',
		},
		{
			id: id(),
			entityId: caseEntity.id,
			key: 'claim-number',
			name: 'Claim Number',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityId: caseEntity.id,
			key: 'complexity',
			name: 'Complexity',
			type: 'option',
			options: ['Standard', 'Complex'],
		},
		{
			id: id(),
			entityId: caseEntity.id,
			key: 'closed-date',
			name: 'Closed Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityId: caseEntity.id,
			key: 'status',
			name: 'Status',
			type: 'option',
			options: ['Pending', 'Open', 'Closed'],
		},
	]

	caseFields.forEach(db.fields.save)

	// EVENTS

	const eventFields: FieldModel[] = [
		{
			id: id(),
			entityId: eventEntity.id,
			key: 'start-date',
			name: 'Start Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityId: eventEntity.id,
			key: 'end-date',
			name: 'End Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityId: eventEntity.id,
			key: 'type',
			name: 'Type',
			type: 'option',
			options: ['Review', 'Mediation', 'CMC'],
			defaultValue: 'CMC',
		},
		{
			id: id(),
			entityId: eventEntity.id,
			key: 'decision-date',
			name: 'Decision Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityId: eventEntity.id,
			key: 'postponed',
			name: 'Postponed',
			type: 'boolean',
			options: [],
		},
	]

	eventFields.forEach(db.fields.save)

	// USERS

	const userFields: FieldModel[] = [
		{
			id: id(),
			entityId: userEntity.id,
			key: 'first-name',
			name: 'First Name',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityId: userEntity.id,
			key: 'last-name',
			name: 'Last Name',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityId: userEntity.id,
			key: 'email',
			name: 'Email',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityId: userEntity.id,
			key: 'phone',
			name: 'Phone',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityId: userEntity.id,
			key: 'active',
			name: 'Active',
			type: 'boolean',
			options: [],
			defaultValue: 'true',
		},
	]

	userFields.forEach(db.fields.save)

	// ACTIONS

	// CASE

	const caseActions: ActionModel[] = [
		{
			id: id(),
			entityId: caseEntity.id,
			key: 'close-case',
			name: 'Close Case',
			changes: [],
		},
	]

	caseActions.forEach(db.actions.save)

	// EVENTS

	const eventActions: ActionModel[] = []

	eventActions.forEach(db.actions.save)

	// CONNECTIONS

	db.relations.save({
		id: id(),
		key: 'events',
		name: 'Events',
		entityId: caseEntity.id,
		relatedId: eventEntity.id,
		type: 'one-to-many',
	})

	db.relations.save({
		id: id(),
		key: 'attendees',
		name: 'Attendees',
		entityId: eventEntity.id,
		relatedId: userEntity.id,
		type: 'one-to-many',
	})

	db.relations.save({
		id: id(),
		key: 'applicant',
		name: 'Applicant',
		entityId: userEntity.id,
		relatedId: caseEntity.id,
		type: 'one-to-many',
	})

	// NOTIFICATIONS

	db.notifications.save({
		id: id(),
		name: 'Event Reminder 2 days',
		focus: eventEntity.id,
		enabled: true,
		triggerType: 'date',
		triggerRepeat: 2,
		triggerEvery: '',
		triggerYearIn: '',
		triggerMonthOn: '',
		triggerWeekOn: '',
		triggerDayAt: '',
		triggerAction: '',
		obeyTriggerTiming: true,
		triggerTimingType: 'before',
		triggerTiming: 'day',
		triggerDate: db.fields.find((x) => x.entityId === eventEntity.id && x.key === 'start-date')?.id || '',
		ruleGroups: [
			{
				rules: [
					{
						entity: eventEntity.id,
						field: db.fields.find((x) => x.entityId === eventEntity.id && x.key === 'postponed')?.id || '',
						operator: 'false',
						value: '',
					},
				],
			},
		],
		subject: 'Upcoming Conference',
		content:
			'<h1>Hello {{recipient-first-name}}</h1><p><br></p><p>You have a {{event-type}} coming up on {{event-start-date}}</p>',
	})

	db.templates.save({
		id: id(),
		name: 'Event Reminder 2 days',
		content:
			'<h1>Hello {{recipient-first-name}}</h1><p><br></p><p>You have a {{event-type}} coming up on {{event-start-date}}</p>',
	})

	const adminRole = db.roles.save({
		id: id(),
		name: 'Administrator',
	})

	const caseManagerRole = db.roles.save({
		id: id(),
		name: 'Case Manager',
	})

	const reviewerRole = db.roles.save({
		id: id(),
		name: 'Reviewer',
	})

	const applicantRole = db.roles.save({
		id: id(),
		name: 'Applicant',
	})

	;([adminRole, caseManagerRole, reviewerRole] as RoleModel[]).forEach((role) => {
		db.entities.all().forEach((entity) => {
			db.entityPermissions.save({
				id: id(),
				entityId: entity.id,
				roleId: role.id,
				view: true,
				modify: true,
				remove: true,
			})

			db.fields
				.all((x) => x.entityId === entity.id)
				.forEach((entityField) => {
					db.fieldPermissions.save({
						id: id(),
						fieldId: entityField.id,
						roleId: role.id,
						view: true,
						modify: true,
					})
				})

			db.actions
				.all((x) => x.entityId === entity.id)
				.forEach((entityAction) => {
					db.actionPermissions.save({
						id: id(),
						actionId: entityAction.id,
						roleId: role.id,
						perform: true,
					})
				})

			db.relations
				.all((x) => x.entityId === entity.id)
				.forEach((relation) => {
					db.relationPermissions.save({
						id: id(),
						relationId: relation.id,
						roleId: role.id,
						view: true,
						modify: true,
						remove: true,
					})
				})
		})
	})

	db.entities.all().forEach((entityType) => {
		db.entityPermissions.save({
			id: id(),
			entityId: entityType.id,
			roleId: applicantRole.id,
			view: true,
			modify: false,
			remove: false,
		})

		db.fields
			.all((x) => x.entityId === entityType.id)
			.forEach((entityField) => {
				db.fieldPermissions.save({
					id: id(),
					fieldId: entityField.id,
					roleId: applicantRole.id,
					view: true,
					modify: false,
				})
			})

		db.actions
			.all((x) => x.entityId === entityType.id)
			.forEach((entityAction) => {
				db.actionPermissions.save({
					id: id(),
					actionId: entityAction.id,
					roleId: applicantRole.id,
					perform: false,
				})
			})

		db.relations
			.all((x) => x.entityId === entityType.id)
			.forEach((relation) => {
				db.relationPermissions.save({
					id: id(),
					relationId: relation.id,
					roleId: applicantRole.id,
					view: true,
					modify: false,
					remove: false,
				})
			})
	})
}

if (!localStorage.getItem('data')) {
	seed()
}
